import React from 'react'
import {Link} from 'react-router-dom'
import './App.css';

const NavBar= () => (
    
    <nav>
    <header>
        <ul className="nav_links">
            <li>
                <Link to="/">Home</Link>
            </li>
            <li>
                <Link to="/about">About</Link>
            </li>
            <li>
                <Link to="/articles-list">Articles</Link>
            </li>
        </ul>
        </header>
    </nav>
    
);

export default NavBar;