import React, {useState} from 'react';

const AddCommentForm = ({articleName, setArticleInfo}) => {

    const [username, setUsername] = useState('');
    const [commentText, setCommentText] = useState('');

    const addComment = async () => {
        const result = await fetch(`/api/articles/${articleName}/add-comment`, {
            method: 'post',
            body: JSON.stringify({username, text: commentText}),
            headers: {
                'Content-Type': 'application/json',
            }
        });

        const body = await result.json();
        setArticleInfo(body);
        setUsername('');
        setCommentText('');
    }
    
    return (
    <div>
    <h3>Add a comment</h3>
    <table>
        <tr>
        <td>Name:</td>
        <td><input type="text" value={username} onChange = {(event) => setUsername(event.target.value)} /></td>
        </tr>
        <tr>
            <td>Comment:</td>
            <td> <textarea rows="4" cols="50" value={commentText} onChange = {(event) => setCommentText(event.target.value)}/></td>
        </tr>
        <tr>
            <td colSpan="2" align="right"><button onClick = {() =>addComment()}>Add Comments</button></td>
        </tr>
        
    </table>
    </div>
);
    }

export default AddCommentForm;